*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}
button{
  cursor: pointer;
}
.App{
  overflow: hidden;
  width: 100vw;
  height: 100vh;
}

@media screen and (max-width: 505px) {
  input[type="text"]{
    box-shadow: 0 0 5px #c0c0c0;
    -webkit-box-shadow: 0 0 5px #c0c0c0;
    -moz-box-shadow: 0 0 5px #c0c0c0;
  }
}


.sidebar__top input {
  -webkit-box-shadow: 0px 0px 15px 10px rgba(192, 192, 192, 0.2) !important;
  -moz-box-shadow: 0px 0px 15px 10px rgba(192, 192, 192, 0.2) !important;
  box-shadow: 0px 0px 15px 10px rgba(192, 192, 192, 0.2) !important;
}